.card-custom-description {
  overflow: hidden;       /* Hides overflow content */
  text-overflow: ellipsis; /* Adds "..." for truncated text */
  display: -webkit-box;   /* Required for limiting lines */
  -webkit-line-clamp: 5;  /* Limits text to 3 lines */
  -webkit-box-orient: vertical; /* Sets vertical layout for the box */
}

.custom-horizontal-line {
  width: -webkit-fill-available; /* Use the full available width */
  border: 0; /* Remove the default border */
  height: 1px; /* Set the thickness of the line */
  background-color: #ccc; /* Adjust the color as needed */
  margin: 10px 0; /* Add spacing around the <hr> */
}

.card {
  display: inline-flex;
  justify-content: stretch;
  align-items: center;
  flex-direction: column;
  width: 350px;
  max-width: calc(100% - 20px - 20px);
  margin: 20px;
  background: var(--background);
  border-radius: var(--rounded);
  overflow: hidden;
  box-shadow: var(--shadow);
  vertical-align: top;
}

.card[data-style="small"] {
  width: 250px;
}

.card-image img {
  aspect-ratio: 3 / 2;
  object-fit: cover;
  width: 100%;
  // box-shadow: var(--shadow);
}

.card-text {
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  gap: 20px;
  max-width: 100%;
  padding: 20px;
}

.card-text > *,
.card-text > .tags {
  margin: 0;
}

.card-title {
  font-family: var(--heading);
  font-weight: var(--semi-bold);
}

.card-subtitle {
  margin-top: -15px;
  font-style: italic;
}
